export class Usuario {

    constructor(usuariosid=0, usuariosnom='', usuariospas='', usuariosper=0, usuariosmai='', usuariosali='') {
        this.usuariosid  = usuariosid;
        this.usuariosnom = usuariosnom;
        this.usuariospas = usuariospas;
        this.usuariosper = usuariosper;
        this.usuariosmai = usuariosmai;
        this.usuariosali = usuariosali;
    }
    usuariosid: number;
    usuariosnom: string;
    usuariospas: string;
    usuariosper: number;
    usuariosmai: string;
    usuariosali: string;
}